import TOSBase from "../base";
import { filterUndefined } from "../utils";


export enum DatasetTemplateId {
  BasicInfoTemplate = 'basic_info_template',
  ImageSemanticTemplate = 'image_semantic_template',
}
/**
 * 
 */
export interface CreateDatasetParams {
  accountId: string;
  /**
   * Dataset
   * @required 
   * @constraints 1~128(-)(_)
   */
  DatasetName: string;

  /**
   * 
   * @required 
   * @constraints 256
   */
  Description: string;

  /**
   * ID
   * @required 
   * @enum basic_info_template: 
   * @enum image_semantic_template: ++
   */
  TemplateId: DatasetTemplateId;
  Region?: string;
}

/**
 * Dataset
 */
export interface Dataset {
  /** Dataset */
  DatasetName: string;
  /** Bucket */
  BindCount: number;
  /** RFC3339Nano  */
  CreateTime: string;
  /** RFC3339Nano  */
  UpdateTime: string;
  /**  */
  Description: string;
  /**  */
  FileCount: number;
  /**  */
  TotalFileSize: number;
  /** ID */
  TemplateId: string;
  Region?: string;
}

/**
 * Dataset
 */
export interface CreateDatasetOutput {
  /** RequestId */
  RequestId: string;
  /** Dataset */
  Dataset: Dataset;
}
/**
 * Dataset
 * @param this TOSBase
 * @param input CreateDatasetParams
 * @returns CreateDatasetOutput
 * @private unstable 
 */
export async function createDataset(
  this: TOSBase,
  input: CreateDatasetParams
) {
  const { accountId, ...rest } = input;
  const res = await this.fetch<CreateDatasetOutput>(
    'POST',
    '/dataset',
    {},
    {
      'x-tos-account-id': accountId,
    },
    filterUndefined(rest),
    {}
  );
  return res;
}


export interface GetDatasetParams {
  datasetname: string;
  accountId: string;
}

export interface GetDatasetOutput {
  RequestId: string;
  Dataset: Dataset;
}

/**
 * Dataset
 * @param this TOSBase
 * @param input GetDatasetParams
 * @returns GetDatasetOutput
 * @private unstable 
 */
export async function getDataset(
  this: TOSBase,
  input: GetDatasetParams
) {
  const { datasetname, accountId } = input;
  const res = await this.fetch<GetDatasetOutput>(
    'GET',
    '/dataset',
    {datasetname},
    {
      'x-tos-account-id': accountId,
    },
  );
  return res;
}

/**
 * Dataset
 */
export interface DeleteDatasetParams {
  accountId: string;
  /**
   * Dataset
   * @required 
   */
  datasetname: string;
}

/**
 * Dataset
 */
export interface DeleteDatasetOutput {
  /** RequestId */
  RequestId: string;
  /** Dataset */
  Dataset: Dataset;
}

/**
 * Dataset
 * @param this TOSBase
 * @param input DeleteDatasetParams
 * @returns DeleteDatasetOutput
 * @private unstable 
 */
export async function deleteDataset(
  this: TOSBase,
  input: DeleteDatasetParams
) {
  const { accountId, datasetname } = input;
  const res = await this.fetch<DeleteDatasetOutput>(
    'DELETE',
    '/dataset',
    { datasetname },
    {
      'x-tos-account-id': accountId,
    }
  );
  return res;
}

/**
 * 
 * @interface ListDatasetParams
 * @property {number} [maxresults] - 1000~100
 * @property {string} [prefix] - ""
 * @property {string} [nexttoken] - marker""
 */
export interface ListDatasetParams {
  accountId: string;
  maxresults?: number;
  prefix?: string;
  nexttoken?: string;
}

/**
 * 
 * @interface ListDatasetOutput
 * @property {string} RequestId - request id
 * @property {string} [NextToken] - marker
 * @property {Dataset[]} Datasets - 
 */
export interface ListDatasetOutput {
  RequestId: string;
  NextToken?: string;
  Datasets: Dataset[];
}
/**
 * 
 * @param this TOSBase
 * @param input ListDatasetParams
 * @returns ListDatasetOutput
 * @private unstable 
 */
export async function listDataset(
  this: TOSBase,
  input: ListDatasetParams
) {
  const { accountId, ...rest } = input;
  const res = await this.fetch<ListDatasetOutput>(
    'GET',
    '/datasets',
    filterUndefined(rest),
    {
      'x-tos-account-id': accountId,
    }
  );
  return res;
}
