import TOSBase from "../base";
import { FileType } from "./type";
import { filterUndefined } from "../utils";


/**
 * 
 * 
 */
export enum Operator {
  /**  */
  NOT = 'not',

  /**  -  */
  OR = 'or',

  /**  -  */
  AND = 'and',

  /**  -  */
  LT = 'lt',

  /**  -  */
  LTE = 'lte',

  /**  -  */
  GT = 'gt',

  /**  -  */
  GTE = 'gte',

  /**  -  */
  EQ = 'eq',

  /**  -  */
  EXIST = 'exist',

  /**  -  */
  PREFIX = 'prefix',

  /**  -  */
  MATCH_PHRASE = 'match-phrase',

  /**  -  */
  // NESTED = 'nested',
}


export interface Query {
  Operation: Operator;
  Field?: string;
  Value?: string;
  SubQueries?: Query[];
}

export enum AGGREGATION_OPERATION {
  MIN = 'min',
  MAX = 'max',
  AVERAGE = 'average',
  SUM = 'sum',
  COUNT = 'count',
  DISTINCT = 'distinct',
  GROUP = 'group',
}


export interface Group {
  Value: string;
  Count: number;
}


export interface AggregationsRes {
  Field: string;
  Operation: string;
  Value?: number;
  Groups?: Group[];
}

export interface AggregationsParams {
  Field?: string;
  Operation?: AGGREGATION_OPERATION;
}


/**
 * 
 * @interface DatasetSimpleQueryParams
 * @property {string} DatasetName - 
 * @property {Query} [Query] - 
 * @property {string} [Sort] - 
 * @property {string} [Order] - 
 * @property {number} [MaxResults] - 
 * @property {string} [NextToken] - token
 * @property {Aggregations[]} [Aggregations] - 
 * @property {string[]} [WithFields] - 
 */
export interface DatasetSimpleQueryParams {
  accountId: string;
  /**  */
  DatasetName: string;
  
  /**  SubQueries */
  Query?: Query;
  
  /**
   * 
   * @example "Size,Filename"
   * @description 5
   */
  Sort?: string;
  
  /**
   * 
   * @example "asc,desc"
   * @description ascdesc
   */
  Order?: string;
  
  /**
   * 
   * @description Aggregations0~100100Aggregations0~2000
   */
  MaxResults?: number;
  
  /**
   * token
   * @description NextToken
   */
  NextToken?: string;
  
  /**
   * 
   * @description Aggregation
   */
  Aggregations?: AggregationsParams[];
  
  /**
   * 
   * @description 
   */
  WithFields?: string[];
}


export type ObjectFile = FileType;

export interface DatasetSimpleQueryOutput {
  /**  ID */
  RequestId: string;
  /**
   * 
   * MaxResults
   */
  NextToken?: string;
  /**
   * 
   * Aggregations
   */
  Aggregations?: AggregationsRes[];
  /**
   * 
   * Aggregations
   */
  Files?: ObjectFile[];
}

export async function datasetSimpleQuery(
  this: TOSBase,
  input: DatasetSimpleQueryParams
) {
  const { accountId, ...rest } = input;
  const res = await this.fetch<DatasetSimpleQueryOutput>(
    'POST',
    '/datasetquery',
    {mode: 'SimpleQuery'},
    {
      'x-tos-account-id': accountId,
    },
    filterUndefined(rest)
  );
  return res;
}




/**
 * 
 */
export enum SEMANTIC_QUERY_TYPE {
  TEXT = 'text',
  IMAGE = 'image',
}

/**
 * 
 * @interface DatasetSemanticQueryParams
 */
export interface DatasetSemanticQueryParams {
  accountId: string;
  /**
   * 
   */
  DatasetName: string;
  /**
   * 
   * - SemanticQueryTypetext
   * - SemanticQueryTypeimageurl
   */
  SemanticQueryInput: string;
  /**
   * 
   * - text60
   * - image
   * @default text
   */
  SemanticQueryType?: SEMANTIC_QUERY_TYPE;
  /**
   * SubQueries
   * @constraints 1005
   */
  Query?: Query;
  /**
   * 
   * @default 100
   * @constraints 0~100
   */
  MaxResults?: number;
  /**
   * 
   * @constraints 
   */
  WithFields?: string[];
}

/**
 * 
 * @interface DatasetSemanticQueryOutput
 */
export interface DatasetSemanticQueryOutput {
  /**
   * RequestId
   */
  RequestId: string;
  /**
   * 
   * @description Aggregations
   */
  Files?: ObjectFile[];
}

export async function datasetSemanticQuery(
  this: TOSBase,
  input: DatasetSemanticQueryParams
) {
  const { accountId, ...rest } = input;
  const res = await this.fetch<DatasetSemanticQueryOutput>(
    'POST',
    '/datasetquery',
    { mode: 'SemanticQuery'},
    {
      'x-tos-account-id': accountId,
    },
    filterUndefined(rest)
  );
  return res;
}
